import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';
import { NavigationBar } from './components/NavigationBar';
import { Home } from './Home';
import { AboutUs } from './AboutUs';
import { LabelSticker } from './LabelSticker';
import { OrganicJuice } from './OrganicJuice';
import { TakeawayMaterials } from './TakeawayMaterials';
import { Social } from './Social';
import { SourcingAgent } from './SourcingAgent';
import { PrivacyPolicy } from './PrivacyPolicy';
import { NoMatch } from './NoMatch';
import { Layout } from './components/Layout';
import { Footer } from './components/Footer';


class App extends Component {
  render() {
    return (
      <React.Fragment>
        <Router>
          <NavigationBar />
          <Layout>
            <Routes>
            <Route path="/" element={<Home />} />
              <Route path="/SourcingAgent" element={<SourcingAgent />} />
              <Route path="/LabelSticker" element={<LabelSticker />} />
              <Route path="/OrganicJuice" element={<OrganicJuice />} />
              <Route path="/TakeawayMaterials" element={<TakeawayMaterials />} />
              <Route path="/Social" element={<Social />} />
              <Route path="/AboutUs" element={<AboutUs />} />
              <Route path="/PrivacyPolicy" element={<PrivacyPolicy />} />
              <Route path='*' element={<NoMatch />} />
            </Routes>
            <Footer />
          </Layout>
        </Router>
      </React.Fragment>
    );
  }
}

export default App;
